@import "../../../css/module/color";
@import "../../../css/module/fn";

body {
	background: {
		image: url("../assets/bg-login.jpg");
		size: 100% 100%;
	}
}

#mainSection {
	border-radius: 0.2rem;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 9.2rem;
	height: 13.5rem;
	//box-shadow: 0 10px 8px 0 rgba(255, 255, 255, 0.75);
	margin: {
		top: -6.8rem;
		left: -4.6rem;
	}
;
	background: {
		color: #faf7f7;
		image: url("/img/logo-itb.png");
		repeat: no-repeat;
		position: center 2rem;
		size: 3.6rem 2rem;
	}
	
	#swLgn {
		position: absolute;
		top: p2m(35);
		right: p2m(30);
		/*border-radius: 4px;
		width: 72px;
		border-width: 1px;
		.mui-switch-handle {
			border-radius: 5px;
			box-shadow: none;
			border: 1px solid #ddd;
			//height: 26px;
		}*/
	}
}

#form1 {
	position: absolute;
	top: p2m(380);
	left: 50%;
	width: p2m(516);
	margin-left: p2m(-516/2);
	
	#username, #password {
		border: 2px solid;
		border-color: $snow;
		border-radius: 3px;
		background-repeat: no-repeat;
		background-size: 22px 22px;
		background-position: 8px center;
		
		//text-indent: 32px;
		padding-left:32px;
		&:focus {
			border-color: $red;
		}
	}
	#username {
		background-image: url("./ico32-user.png");
	}
	
	#password {
		background-image: url("./ico32-lock.png");
	}
	
	.mui-radio.mui-left input[type='radio'], .mui-checkbox.mui-left input[type='checkbox'] {
		left: 0;
	}
	.mui-radio.mui-left label, .mui-checkbox.mui-left label {
		padding-left: 28px;
		font-size: 17px;
	}
	
	.mui-checkbox input[type='checkbox']:before {
		content: '__scss_backup_0';
		color: $red;
	}
	.mui-radio input[type='radio'], .mui-checkbox input[type='checkbox'] {
		top: 8px;
	}
	.mui-radio input[type='radio']:before, .mui-checkbox input[type='checkbox']:before {
		font-size: 22px;
	}
	.mui-radio input[type='radio']:checked:before, .mui-checkbox input[type='checkbox']:checked:before {
		content: '__scss_backup_1';
	}
	
	#btnLogin {
		width: 100%;
		height: 42px;
		font-size: 18px;
		font-weight: 600;
		margin-top: 0.5rem;
	}
	
	.linkBlack {
		color: $font-black;
		font: {
			size: 15px;
		}
	}
	#guestLogin {
		float: right;
	}
	
}
